@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  color: #363945;
  font-size: max(2vh, 16px);
  margin-bottom: 3vh;
}


.main {
  background-color: lightgray;
  height: 100vh;
  display: inline-flex;
  touch-action: pan-y !important;
}

/* Desktops and laptops ----------- */
@media only screen and (min-width: 850px) {
  .presentation {
    font-family: Barlow;
    padding-left: 10vw;
    padding-top: 10vw;
    width: 90vw;
  }
  
  .presentation .name {
    font-size: max(7vh, 40px);
    color: #363945;
    height: 7vh;
    font-weight: 700;
    display: inline-block;
  }
  .presentation .subtitle {
    font-size: max(2.5vh, 24px);
    font-weight: 500;
    color: dimgray;
    margin-top: 1vh;
    display: block;
  }
  
  .presentation .letter {
    float: left;
    width: 35vw;
    height: auto;
    margin-top: 7vh;
    margin-right: 10vw;
  }

  .presentation .letter .greetings {
    margin-bottom: 6vh;
  }

  .presentation .letter .notice {
    margin-top: 5vh;
    margin-left: 30px;
  }
}

/* mobiles (landscape and portrait) */
@media only screen and (max-width: 850px) {
  .presentation {
    font-family: Barlow;
    padding-left: 10vw;
    padding-top: 15vh;
    width: 100vw;
    display: inline-table;
  }
  
  .presentation .name {
    font-size: 30px;
    color: #363945;
    height: 28px;
    font-weight: 700;
  }
  .presentation .subtitle {
    font-size: 18px;
    font-weight: 500;
    color: dimgray;
    margin-top: 1vh;
  }
  
  .presentation .letter {
    width: 80vw;
    height: auto;
    margin-top: 7vh;
    color: #363945;
    font-size: 16px;
  }

  .presentation .letter .notice {
    margin-top: 10vh;
  }

}


